@font-face {
  font-family: TTNorms-Bold;
  src: url(../fonts/TTNorms-Bold.ttf);
  src: url(../fonts/TTNorms-Bold.woff);
  src: url(../fonts/TTNorms-Bold.woff2);
  font-style: normal; }

@font-face {
  font-family: TTNorms-ExtraBold;
