.direction {
  padding-bottom: 120px; }
  .direction__wraptitle {
    display: flex;
    align-items: center; }
  .direction__title {
    padding: 80px 0;
    display: inline;
    font-weight: bold;
    font-family: TTNorms-Bold;
    font-size: 46px;
    line-height: 150%; }
  .direction__cartwrap {
    display: flex;
    justify-content: space-between; }
  .direction__card {
    overflow: hidden;
    margin: 0 5px;
    z-index: 0; }
  .direction__imgcard img {
    transition: all 1s ease-out;
    width: 100%;
    height: auto; }
  .direction__imgcard:hover .direction__linkto {
    background-color: #32A1DF; }
  .direction__imgcard:hover img {
    transform: scale(1.2); }
  .direction__linkto {
    position: relative;
    background-color: #118BDD;
    display: flex;
    align-items: center;
    height: 60px;
    width: 100%; }
    .direction__linkto a {
      width: 100%;
      max-height: 60px;
      margin: 0 5px;
      font-family: TTNorms-Regular;
      text-decoration: none;
      font-weight: 500;
      font-size: 18px;
      line-height: 150%;
      color: #FFF; }
  .direction__arrow {
    background: url("../img/arrow.png") no-repeat 50% 50%;
    width: 80px;
    height: 60px;
    background-color: #32A1DF; }

.partner__slider {
  padding-bottom: 100px; }

.partner__cardslide {
  margin: 10px;
  display: flex !important;
  justify-content: center;
  align-items: center !important;
  background-color: #FAFAFA;
  width: 200px !important;
  height: 100px !important; }

.prev {
  background: url("../img/path 3.svg") no-repeat 50% 50%;
  transition: all 0.5s; }

.next {
  background: url("../img/path 3.svg") no-repeat 50% 50%;
  transform: rotate(180deg); }

.next:hover {
  background: url("../img/arrow.svg") no-repeat 50% 50%;
  background-color: #118BDD;
  transform: rotate(360deg); }

.prev:hover {
  background: url("../img/arrow.svg") no-repeat 50% 50%;
  background-color: #118BDD;
  transform: rotate(180deg); }

.btn-no-hover {
  background-color: white;
  border: 1px solid #C9C9C9;
  width: 65px;
  height: 35px; }

.region2 {
  transform: translate(23.9%, 3.6%); }

.map {
  background-color: #FAFAFA; }
  .map__svg {
    margin-bottom: 100px; }
  .map__cart {
