    .map__caract__property {
      font-family: TTNorms-Bold;
      font-weight: bold;
      font-size: 20px;
      line-height: 24px; }
  .map__wrap {
    width: 60%; }
  .map__btn {
    height: 50px !important; }
  .map__arrow {
    height: 50px !important; }

