.news__wrap {
  display: flex;
  align-items: center;
  justify-content: space-between; }

.news__all {
  padding: 10px 40px; }

.news__all-title:after {
  content: url("../img/arrownews.svg");
  position: relative;
  margin-left: 5px; }

.news__all-title {
